@font-face {
  font-family: "Lato";
  src: url("./fonts/Lato/Lato-Regular.ttf");
}

@font-face {
  font-family: "Lato";
  src: url("./fonts/Lato/Lato-Light.ttf");
  font-style: light;
}

@font-face {
  font-family: "Pacifico";
  src: url("./fonts/PacificoFont/Pacifico.ttf");
}

/*================ Media Query Mixin ================*/
@mixin media-query($media-query) {
  $breakpoint-found: false;

  @each $breakpoint in $grid-breakpoints {
    $name: nth($breakpoint, 1);
    $declaration: nth($breakpoint, 2);

    @if $media-query == $name and $declaration {
      $breakpoint-found: true;

      @media only screen and #{$declaration} {
        @content;
      }
    }
  }

  @if $breakpoint-found == false {
    @warn 'Breakpoint "#{$media-query}" does not exist';
  }
}

/*============================================================================
      Grid Breakpoints and Class Names
        - Do not change the variable names
    ==============================================================================*/
$grid-tiny: 360px; //custom
$grid-medium: 750px;
$grid-large: 990px;
$grid-widescreen: 1400px;
$grid-gutter: 15px;

$tiny: "tiny"; //custom
$small: "small";
$medium: "medium";
$medium-down: "medium-down";
$medium-up: "medium-up";
$large: "large";
$large-down: "large-down";
$large-up: "large-up";
$widescreen: "widescreen";

// The `$grid-breakpoints` list is used to build our media queries.
// You can use these in the media-query mixin.
$grid-breakpoints: (
  $tiny "(max-width: #{$grid-tiny})",
  $small "(max-width: #{$grid-medium - 1})",
  $medium "(min-width: #{$grid-medium}) and (max-width: #{$grid-large - 1})",
  $medium-down "(max-width: #{$grid-large - 1})",
  $medium-up "(min-width: #{$grid-medium})",
  $large "(min-width: #{$grid-large}) and (max-width: #{$grid-widescreen - 1})",
  $large-down "(max-width: #{$grid-widescreen - 1})",
  $large-up "(min-width: #{$grid-large})",
  $widescreen "(min-width: #{$grid-widescreen})"
);

$ThemeColor: #10504f;

html {
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: "Lato";
  overflow-x: hidden;
}

.h1 {
  font-size: 20px;
  font-weight: normal;
}

.h2 {
  font-size: 16px;
  font-weight: normal;
}

.hide-on-mobile {
  @include media-query($small) {
    display: none !important;
  }
}

.hide-on-desktop {
  @include media-query($medium-up) {
    display: none !important;
  }
}

.mobile-whole {
  width: calc(100% - #{$grid-gutter * 2});
  &:first-child {
    margin-left: 0;
  }
}

.mobile-half {
  @include media-query($small) {
    width: calc(50% - #{$grid-gutter * 1.5});
    margin-left: $grid-gutter;
  }

  &:first-child {
    margin-left: 0;
  }
}

.mobile-third {
  @include media-query($small) {
    // the subtraction is calculated by taking the spacing between and muliplying by the number of spaces by the number of elements
    width: calc(100% / 3 - 9px * 2 / 3);

    margin-left: 9px;

    &:first-child {
      margin-left: 0;
    }
  }
}

.desktop-third {
  @include media-query($medium-up) {
    width: calc(100% / 3 - #{$grid-gutter * 8 / 3});

    margin-left: $grid-gutter * 2;
  }
}

.desktop-quarter {
  @include media-query($medium-up) {
    width: calc(100% / 4 - 25px);
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }
}

.desktop-fifth {
  @include media-query($medium-up) {
    width: calc(100% / 5 - #{20px * 4 / 5});
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }
}

.announcement-bar {
  background-color: black;
  color: white;
  text-align: center;
  vertical-align: middle;
  @include media-query($small) {
    height: 21px;
  }
  @include media-query($medium-up) {
    height: 60px;
  }
}

.announcement-bar span {
  font-style: light;

  @include media-query($small) {
    font-size: 12px;
    line-height: 21px;
  }
  @include media-query($medium-up) {
    font-size: 26px;
    line-height: 60px;
  }
}

.nav-bar {
  background-color: $ThemeColor;
  color: white;
  text-align: center;

  @include media-query($small) {
    height: 45px;
    padding: 4px $grid-gutter;
  }
  @include media-query($medium-up) {
    height: 85px;
    padding: 8px $grid-gutter;
  }
}

.nav-bar div {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
}

.mobile-dropdown {
  float: left;
  margin-top: 11px;
}

.mobile-dropdown img {
  width: 31px;
}

.logo-container img {
  @include media-query($small) {
    width: 54px;
  }
  @include media-query($medium-up) {
    width: 93px;
  }
}

.cart {
  float: right;
  margin-top: 11px;
}

.desktop-nav-menu span {
  margin-left: 34px;
  font-style: light;
  font-size: 28px;
}

.banner {
  min-height: 227px;
  width: 100%;
  background-size: cover;
  position: relative;
}

.banner.top-banner {
  @include media-query($small) {
    background-image: url("./images/HeroImage1Mobile.jpg");
  }
  @include media-query($medium-up) {
    background-image: url("./images/HeroImage1.jpg");
  }
}

.banner.bottom-banner {
  @include media-query($small) {
    background-image: url("./images/HeroImage2Mobile.jpg");
  }
  @include media-query($medium-up) {
    background-image: url("./images/HeroImage2.jpg");
  }
}

.img-frame {
  width: 100%;
  opacity: 0;
}

.banner-info {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: $grid-gutter;
  color: white;

  @include media-query($medium-up) {
    left: 5.5%;
  }
}

.banner-info h1 {
  font-family: Pacifico;

  margin: 0;

  @include media-query($small) {
    font-size: 20px;
  }
  @include media-query($medium-up) {
    font-size: 50px;
  }
}

.banner-info h2 {
  margin-top: 0;

  @include media-query($small) {
    font-size: 20px;
  }
  @include media-query($medium-up) {
    font-size: 40px;
  }
}

.bottom-banner .banner-info {
  @include media-query($small) {
    left: 43%;
  }
  @include media-query($medium-up) {
    top: 45%;
    transform: translateY(-45%);
    left: 62.85%;
  }
}

.banner-info .button {
  background-color: black;
  color: white;
  width: fit-content;
  text-align: center;
  font-weight: bold;

  @include media-query($small) {
    min-width: 78px;
    padding: 8px 10px;
    font-size: 12px;
  }

  @include media-query($medium-up) {
    min-width: 78px;
    padding: 12px 20px;
    font-size: 20px;
  }
}

.bottom-banner .banner-info .button {
  background-color: $ThemeColor;
  min-width: 70px;
}

.banner-dots {
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.dot-box {
  width: 12px;
  height: 12px;
  background-color: #d8d8d8;
  opacity: 70%;
  display: inline-block;
  border: solid #d8d8d8 1px inside;
  margin-left: 9px;

  &.active {
    background-color: white;
    opacity: 1;
    border: solid #979797 1px inside;
  }

  &:first-child {
    margin-left: 0;
  }
}

.collection-block {
  padding-bottom: 0;
  white-space: nowrap;
  width: 100%;
  list-style: none;
  overflow: hidden;

  @include media-query($small) {
    margin: $grid-gutter;
  }

  @include media-query($medium-up) {
    margin: $grid-gutter * 2;
  }
}

.insta {
  margin: 0 15px;

  @include media-query($medium-up) {
    margin: 0 60px 30px 60px;
  }
}

.insta h3 {
  font-family: Pacifico;
  font-weight: 400;
  text-align: center;
  margin-bottom: 5px;
  font-style: light;

  @include media-query($small) {
    font-size: 20px;
    line-height: 35px;
  }
  @include media-query($medium-up) {
    font-size: 35px;
    line-height: 61px;
    margin-bottom: 30px;
  }
}

.insta .collection-block {
  margin: 0;
}

.collection-block div {
  display: inline-block;
  float: left;
  position: relative;

  @include media-query($small) {
    margin-bottom: $grid-gutter;
  }
}

.insta .collection-block div {
  margin-bottom: $grid-gutter * 2;
}

.collection-block .collection-title {
  background-color: $ThemeColor;
  width: 100%;
  color: white;
  font-family: Pacifico;
  text-align: center;
  position: absolute;
  display: block;
  bottom: 0;
  left: 0;
  right: 0;

  @include media-query($small) {
    height: 42px;
    font-size: 19px;
  }
  @include media-query($medium-up) {
    height: 110px;
    font-size: 50px;
  }
}

.collection-block div img {
  width: 100%;
}

.new-arrivals {
  text-align: center;
  position: relative;
  overflow: hidden;

  @include media-query($small) {
    margin: $grid-gutter;
  }
  @include media-query($medium-up) {
    margin: 42px 95px;
  }
}

.new-arrivals h3 {
  font-family: Pacifico;
  font-weight: normal;

  @include media-query($small) {
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 10px;
  }
  @include media-query($medium-up) {
    font-size: 35px;
    margin-top: 0;
    margin-bottom: 10px;
  }
}

.product-slider {
  position: relative;
}

.product-card {
  display: inline-block;
  float: left;
}

.product-card img {
  width: 100%;
}

.product-card span {
  display: block;
}

.product-card .card-title {
  @include media-query($small) {
    font-size: 16px;
  }

  @include media-query($medium-up) {
    font-size: 18px;
  }
}

.product-card .card-description {
  font-style: light;

  @include media-query($small) {
    font-size: 12px;
  }
  @include media-query($medium-up) {
    font-size: 16px;
  }
}

.product-card .card-price {
  font-family: Pacifico;
  color: $ThemeColor;

  @include media-query($small) {
    font-size: 16px;
  }
  @include media-query($medium-up) {
    font-size: 22px;
  }
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  padding: 5px;
}

.arrow img {
  width: 13px;
}

.left-arrow {
  left: 0;
}

.right-arrow {
  right: 0;
}

.newsletter-block {
  background-color: $ThemeColor;
  color: white;
  text-align: center;

  @include media-query($small) {
    padding: 16px 15px 25px 15px;
  }

  @include media-query($medium-up) {
    height: 163px;
    padding: 61px 0 61px 0;
  }
}

.email-icon img {
  @include media-query($small) {
    width: 17px;
  }

  @include media-query($medium-up) {
    width: 36px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 15px;
  }
}

.newsletter-title {
  font-family: Pacifico;
  font-weight: 400;
  text-align: center;

  @include media-query($small) {
    height: 35px;
    font-size: 20px;
    margin-top: -8px;
    line-height: 35px;
  }

  @include media-query($medium-up) {
    display: inline-block;
    font-size: 35px;
  }
}

.newsletter-description {
  color: #ffffff;
  font-family: Lato;

  @include media-query($small) {
    font-size: 10px;
    line-height: 12px;
    margin: 10px auto 15px auto;
    width: 320px;
    font-weight: 300;
  }

  @include media-query($medium-up) {
    font-size: 20px;
    width: 55%;
    display: inline-block;
    vertical-align: middle;
    margin-right: 25px;
    width: 589px;
    height: 60px;
    font-weight: 400;
    line-height: 24px;
  }
}

.newsletter-divider {
  height: 34px;
}

.newsletter-form {
  width: 100%;

  @include media-query($medium-up) {
    width: unset;
    display: inline-block;
  }
}

.newsletter-form input {
  width: calc(70% - 20px);
  margin-right: 5px;
  padding: 11px;
  font-size: 8px;
  font-weight: 400;
  line-height: 10px;

  @include media-query($medium-up) {
    width: 340px;
    font-size: 16px;
    line-height: 19px;
  }
}

.newsletter-form button {
  width: calc(30% - 15px);
  height: 36px;
  vertical-align: top;
  border: solid 1px #ffffff;
  background-color: $ThemeColor;
  color: #ffffff;
  font-family: Pacifico;
  font-size: 10px;
  font-weight: 400;
  line-height: 18px;
  text-align: center;

  @include media-query($medium-up) {
    width: 170px;
    font-size: 20px;
    height: 44px;
    border: solid 2px #ffffff;
  }
}

footer {
  background-color: black;
  padding: 15px 50px;
  color: white;
  @include media-query($medium-up) {
    padding: 45px 60px;
  }
}

.footer-container,
.footer-container div {
  position: relative;
}

.footer-container .footer-title {
  font-family: Pacifico;
  font-size: 20px;
  margin-top: 10px;
}

.footer-container .dropdown-toggle {
  position: absolute;
  right: 0;
  top: 0;
  height: 40px;
  line-height: 40px;
  vertical-align: middle;
  font-family: Pacifico;
  font-weight: bold;
  font-size: 22px;
}

.footer-container ul {
  list-style-type: none;
  margin: 0;
  padding-left: 0;
  font-family: Lato;
  font-style: light;
  @include media-query($small) {
    display: none;
  }
}

.footer-container .active + ul {
  display: block;
}

.footer-container .active .dropdown-toggle.plus {
  display: none;
}
.footer-container .dropdown-toggle.minus {
  display: none;
}
.footer-container .active .dropdown-toggle.minus {
  display: block;
}

.footer-container .social-links {
  @include media-query($medium-up) {
    float: right;
  }
}

.footer-container .social-links .footer-title {
  @include media-query($medium-up) {
    margin-bottom: 10px;
  }
}

.footer-container .social-links img {
  margin-right: 18px;
}

.footer-container .dropdown-toggle {
  @include media-query($medium-up) {
    display: none;
  }
}

.footer-menu {
  @include media-query($medium-up) {
    line-height: 30px;
    font-size: 16px;
    display: inline-block;
    vertical-align: top;
    margin-right: 100px;
  }
}
